import { Component, OnInit } from '@angular/core';
import { BackendApiProvider } from 'src/providers/backend-api/backend-api.service';
import { Utils } from 'src/commons/Utils';
import { ToastText } from 'src/commons/Constants';

@Component({
  selector: 'app-tab1',
  templateUrl: './tab1.page.html',
  styleUrls: ['./tab1.page.scss'],
})
export class Tab1Page {

  private readonly logHeader = "PontosPage>>";

  public points: number;

  constructor(private api: BackendApiProvider,
    private utils: Utils) {
    const logHeader = this.logHeader + "constructor>>";
    Utils.log(logHeader);
    this.getPoints();
  }

  getPoints() {
    this.api.getPoints().then((points: number) => {
      this.points = points;
    }).catch((error) => this.utils.simpleToastError(error + '\n' + ToastText.SOMETHING_WENT_WRONG));
  }

}
